'use client';

import { SVGConfig } from '@/app/page';
import { X, Copy, Check } from 'lucide-react';
import { useState } from 'react';

interface CodeModalProps {
  isOpen: boolean;
  onClose: () => void;
  svgContent: string;
  config: SVGConfig;
  fileName: string;
}

export function CodeModal({ isOpen, onClose, svgContent, config, fileName }: CodeModalProps) {
  const [copiedSection, setCopiedSection] = useState<string | null>(null);

  if (!isOpen) return null;

  const componentName = fileName.replace(/\.[^/.]+$/, '').replace(/[^a-zA-Z0-9]/g, '');
  const capitalizedName = componentName.charAt(0).toUpperCase() + componentName.slice(1);

  const generateReactComponent = () => {
    // 간단한 SVG 처리
    let processedSVG = svgContent
      .replace(/<\?xml[^>]*\?>/g, '')
      .replace(/<!--[^>]*-->/g, '')
      .replace(/stroke-width/g, 'strokeWidth')
      .replace(/fill="[^"]*"/g, `fill="${config.fillColor}"`)
      .replace(/stroke="[^"]*"/g, `stroke="${config.color}"`)
      .trim();

    return `import React, { useState } from 'react';

export function ${capitalizedName}() {
  const [isHovered, setIsHovered] = useState(false);

  const getHoverStyle = () => {
    if (!isHovered) return {};
    switch ('${config.hoverEffect}') {
      case 'scale': return { transform: 'scale(1.1) rotate(${config.rotation}deg)' };
      case 'rotate': return { transform: 'rotate(${config.rotation + 15}deg)' };
      case 'opacity': return { opacity: 0.8 };
      default: return {};
    }
  };

  return (
    <div
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      style={{
        width: '${config.size}px',
        height: '${config.size}px',
        transform: 'rotate(${config.rotation}deg)',
        opacity: ${config.opacity},
        transition: 'all 0.3s ease-in-out',
        ${config.animation !== 'none' ? `animation: '${config.animation} 2s infinite',` : ''}
        ...getHoverStyle()
      }}
    >
      ${processedSVG}
    </div>
  );
}`;
  };

  const generateInlineSVG = () => {
    // 간단한 SVG 처리
    let processedSVG = svgContent
      .replace(/<\?xml[^>]*\?>/g, '')
      .replace(/<!--[^>]*-->/g, '')
      .replace(/fill="[^"]*"/g, `fill="${config.fillColor}"`)
      .replace(/stroke="[^"]*"/g, `stroke="${config.color}"`)
      .trim();

    return `<div
  style={{
    width: '${config.size}px',
    height: '${config.size}px',
    transform: 'rotate(${config.rotation}deg)',
    opacity: ${config.opacity},
    transition: 'all 0.3s ease-in-out',
    ${config.animation !== 'none' ? `animation: '${config.animation} 2s infinite',` : ''}
  }}
  ${config.hoverEffect === 'scale' ? `onMouseEnter={() => this.style.transform = 'scale(1.1) rotate(${config.rotation}deg)'}
  onMouseLeave={() => this.style.transform = 'rotate(${config.rotation}deg)'}` : ''}
  ${config.hoverEffect === 'rotate' ? `onMouseEnter={() => this.style.transform = 'rotate(${config.rotation + 15}deg)'}
  onMouseLeave={() => this.style.transform = 'rotate(${config.rotation}deg)'}` : ''}
  ${config.hoverEffect === 'opacity' ? `onMouseEnter={() => this.style.opacity = '0.8'}
  onMouseLeave={() => this.style.opacity = '${config.opacity}'}` : ''}
>
  ${processedSVG}
</div>`;
  };

  const generateCSS = () => {
    // 간단한 SVG 처리
    let processedSVG = svgContent
      .replace(/<\?xml[^>]*\?>/g, '')
      .replace(/<!--[^>]*-->/g, '')
      .replace(/fill="[^"]*"/g, `fill="${config.fillColor}"`)
      .replace(/stroke="[^"]*"/g, `stroke="${config.color}"`)
      .trim();

    return `.${componentName.toLowerCase()} {
  width: ${config.size}px;
  height: ${config.size}px;
  transform: rotate(${config.rotation}deg);
  opacity: ${config.opacity};
  transition: all 0.3s ease-in-out;
  ${config.animation !== 'none' ? `animation: ${config.animation} 2s infinite;` : ''}
}

${config.hoverEffect === 'scale' ? `.${componentName.toLowerCase()}:hover {
  transform: scale(1.1) rotate(${config.rotation}deg);
}` : ''}
${config.hoverEffect === 'rotate' ? `.${componentName.toLowerCase()}:hover {
  transform: rotate(${config.rotation + 15}deg);
}` : ''}
${config.hoverEffect === 'opacity' ? `.${componentName.toLowerCase()}:hover {
  opacity: 0.8;
}` : ''}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

@keyframes scale {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

@keyframes bounce {
  0%, 20%, 53%, 80%, 100% { transform: translateY(0); }
  40%, 43% { transform: translateY(-30px); }
  70% { transform: translateY(-15px); }
}

/* HTML */
<div class="${componentName.toLowerCase()}">
  ${processedSVG}
</div>`;
  };

  const copyToClipboard = async (text: string, section: string) => {
    try {
      await navigator.clipboard.writeText(text);
      setCopiedSection(section);
      setTimeout(() => setCopiedSection(null), 2000);
    } catch (err) {
      console.error('Failed to copy text: ', err);
    }
  };

  const reactComponent = generateReactComponent();
  const inlineSVG = generateInlineSVG();
  const cssCode = generateCSS();

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
      <div className="bg-gray-900 rounded-lg max-w-4xl w-full max-h-[90vh] overflow-hidden">
        <div className="flex items-center justify-between p-6 border-b border-gray-800">
          <h2 className="text-xl font-semibold text-white">Export Code</h2>
          <button
            onClick={onClose}
            className="p-2 text-gray-400 hover:text-white transition-colors"
          >
            <X className="w-5 h-5" />
          </button>
        </div>

        <div className="p-6 space-y-6 overflow-y-auto max-h-[calc(90vh-120px)]">
          {/* React Component */}
          <div>
            <div className="flex items-center justify-between mb-3">
              <h3 className="text-lg font-medium text-white">React Component</h3>
              <button
                onClick={() => copyToClipboard(reactComponent, 'react')}
                className="flex items-center space-x-2 px-3 py-1.5 bg-gray-800 hover:bg-gray-700 rounded text-sm text-gray-300 transition-colors"
              >
                {copiedSection === 'react' ? (
                  <>
                    <Check className="w-4 h-4" />
                    <span>Copied!</span>
                  </>
                ) : (
                  <>
                    <Copy className="w-4 h-4" />
                    <span>Copy</span>
                  </>
                )}
              </button>
            </div>
            <pre className="bg-gray-950 rounded-lg p-4 text-sm text-gray-300 overflow-x-auto">
              <code>{reactComponent}</code>
            </pre>
          </div>

          {/* Inline SVG */}
          <div>
            <div className="flex items-center justify-between mb-3">
              <h3 className="text-lg font-medium text-white">Inline SVG</h3>
              <button
                onClick={() => copyToClipboard(inlineSVG, 'inline')}
                className="flex items-center space-x-2 px-3 py-1.5 bg-gray-800 hover:bg-gray-700 rounded text-sm text-gray-300 transition-colors"
              >
                {copiedSection === 'inline' ? (
                  <>
                    <Check className="w-4 h-4" />
                    <span>Copied!</span>
                  </>
                ) : (
                  <>
                    <Copy className="w-4 h-4" />
                    <span>Copy</span>
                  </>
                )}
              </button>
            </div>
            <pre className="bg-gray-950 rounded-lg p-4 text-sm text-gray-300 overflow-x-auto">
              <code>{inlineSVG}</code>
            </pre>
          </div>

          {/* CSS */}
          <div>
            <div className="flex items-center justify-between mb-3">
              <h3 className="text-lg font-medium text-white">CSS</h3>
              <button
                onClick={() => copyToClipboard(cssCode, 'css')}
                className="flex items-center space-x-2 px-3 py-1.5 bg-gray-800 hover:bg-gray-700 rounded text-sm text-gray-300 transition-colors"
              >
                {copiedSection === 'css' ? (
                  <>
                    <Check className="w-4 h-4" />
                    <span>Copied!</span>
                  </>
                ) : (
                  <>
                    <Copy className="w-4 h-4" />
                    <span>Copy</span>
                  </>
                )}
              </button>
            </div>
            <pre className="bg-gray-950 rounded-lg p-4 text-sm text-gray-300 overflow-x-auto">
              <code>{cssCode}</code>
            </pre>
          </div>
        </div>
      </div>
    </div>
  );
}